import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { HomeClientComponent } from './home.component';
import { Routes, RouterModule } from '@angular/router';

//Khởi tạo Model page cần khai báo router , 
const routes: Routes = [
    { path: '', component: HomeClientComponent },
  ];
  
@NgModule({
  imports: [
    CommonModule,  
    RouterModule.forChild(routes)  
  ],
  exports: [
    HomeClientComponent,
    RouterModule
  ],
  declarations: [
    HomeClientComponent
  ],
  providers: [],
})
export class HomeClientModule { }